import { CardContent } from '@material-ui/core';
import CardUI from '../ui/CardUI';
import WeatherCardDetailsData from './WeatherCardDetailsData';
import WeatherCardDetailsList from './WeatherCardDetailsList';

const WeatherCardDetails = ({ weathersData }) => {
  return (
    <CardUI>
      <CardContent>
        <WeatherCardDetailsData weathersData={weathersData} />
        <br />
        <WeatherCardDetailsList weathersData={weathersData} />
      </CardContent>
    </CardUI>
  );
};

export default WeatherCardDetails;
