import { API_BASE_URL, API_KEY } from './api/config';
import { Alert, AlertTitle } from '@material-ui/lab';
import {
  Backdrop,
  Box,
  CircularProgress,
  Container,
  Grid,
  Slide,
  Snackbar,
  Typography,
  makeStyles,
} from '@material-ui/core';

import CityInput from './components/CityInput';
import CountryInput from './components/CountryInput';
import WeatherList from './components/WeatherList';
import useFetch from './hooks/useFetch';
import { useState } from 'react';

const useStyles = makeStyles(theme => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
  snackbar: {
    textAlign: 'initial',
  },
}));

const App = () => {
  const classes = useStyles();

  const [selectedCountry, setSelectedCountry] = useState(null);

  const [selectedCity, setSelectedCity] = useState(null);
  const [cityOptions, setCityOptions] = useState([]);
  const [isCityOptionsDisabled, setIsCityOptionsDisabled] = useState(true);

  const { data, error, isLoading, setUrl, setData, setError } = useFetch();

  const searchCityHandler = city => {
    if (city) {
      setUrl(
        `${API_BASE_URL}/data/2.5/forecast?q=${city}&appid=${API_KEY}&units=metric`
      );
    } else {
      setUrl(null);
      setData(null);
      setError(null);
    }
  };
  const onCloseHandler = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setError(null);
  };

  const getContent = () => {
    if (error) {
      return (
        <Snackbar
          className={classes.snackbar}
          open
          autoHideDuration={10000}
          TransitionComponent={Slide}
          onClose={onCloseHandler}
        >
          <Alert severity="error" onClose={onCloseHandler}>
            <AlertTitle>Error</AlertTitle>
            {error}
          </Alert>
        </Snackbar>
      );
    }

    if (!data && isLoading) {
      return (
        <Backdrop className={classes.backdrop} open>
          <CircularProgress color="inherit" />
        </Backdrop>
      );
    }

    if (!data) {
      return null;
    }

    return <WeatherList weathersData={data.list} cityData={data.city} />;
  };

  return (
    <Box marginTop={2} marginBottom={2}>
      <Container className="App">
        <Typography variant="h4" component="h2" align="center">
          Check Your Weather
        </Typography>

        <Box marginTop={2} marginBottom={2}>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={6} md={6}>
              <CountryInput
                onSearch={searchCityHandler}
                setSelectedCountry={setSelectedCountry}
                setSelectedCity={setSelectedCity}
                setCityOptions={setCityOptions}
                setIsCityOptionsDisabled={setIsCityOptionsDisabled}
              />
            </Grid>
            <Grid item xs={12} sm={6} md={6}>
              <CityInput
                onSearch={searchCityHandler}
                selectedCity={selectedCity}
                setSelectedCity={setSelectedCity}
                cityOptions={cityOptions}
                setCityOptions={setCityOptions}
                isCityOptionsDisabled={isCityOptionsDisabled}
                selectedCountry={selectedCountry}
              />
            </Grid>
          </Grid>
        </Box>

        {getContent()}
      </Container>
    </Box>
  );
};

export default App;
