import {
  CardActionArea,
  CardActions,
  CardContent,
  CardHeader,
  CardMedia,
  IconButton,
  Tooltip,
  Typography,
  Zoom,
  makeStyles,
} from '@material-ui/core';
import { handleImageError, titleCase } from '../utility/utility';

import CardUI from '../ui/CardUI';
import ShareIcon from '@material-ui/icons/Share';
import moment from 'moment';

const useStyles = makeStyles(() => ({
  card: {
    width: '200px',
  },
  card_media: {
    display: 'initial',
    width: '50%',
  },
  card_action_area: {
    textAlign: 'center',
  },
}));

const WeatherCard = ({ weatherData }) => {
  const classes = useStyles();

  const date = moment(weatherData.dt_txt);

  return (
    <CardUI className={classes.card}>
      <CardActionArea className={classes.card_action_area}>
        <CardHeader align="center" title={date.format('dddd')}></CardHeader>
        <CardMedia
          className={classes.card_media}
          component="img"
          image={`http://openweathermap.org/img/wn/${weatherData.weather[0].icon}@2x.png`}
          title={titleCase(weatherData.weather[0].description)}
          alt={weatherData.weather[0].description}
          onError={e => handleImageError(e, weatherData.weather[0].description)}
        ></CardMedia>
        <CardContent>
          <Typography variant="h5" component="h2" align="center">
            {weatherData.main.temp}
            &deg;
          </Typography>
        </CardContent>
      </CardActionArea>
      <CardActions disableSpacing>
        <Tooltip
          title="Share"
          TransitionComponent={Zoom}
          arrow
          disableFocusListener
        >
          <IconButton aria-label="share">
            <ShareIcon />
          </IconButton>
        </Tooltip>
      </CardActions>
    </CardUI>
  );
};

export default WeatherCard;
